<template>
  <div class="contact">
    <h1>聯絡我們</h1>
    <div class="contact__container">
      <div class="contact__block">
        <div class="contact__block__header">
          社交平台
        </div>
        <div class="contact__block__body">
          <a href="#" class="contact__block__sharelink">
            <img src="@/assets/icon/line_logo.png" alt="">
          </a>
          <a href="#" class="contact__block__sharelink">
            <img src="@/assets/icon/facebook_logo.png" alt="">
          </a>
        </div>
      </div>
      <div class="contact__block contact__block--rwdControl"
      v-if="user.id === ''">
        <div class="contact__block__header">
          登入會員
        </div>
        <div class="contact__block__body">
          <login></login>
        </div>
      </div>
      <div class="contact__block contact__block--rwdControl">
        <div class="contact__block__header"
        v-if="user.id === ''">
          以訪客身分留言
        </div>
        <div class="contact__block__header"
        v-if="user.id !== ''">
          快速留言
        </div>
        <div class="contact__block__body contact__block__body--rwdControl">
          <form @submit.prevent='sendMail'>
            <div class="myForm">
              <label for="name">
                <div class="myForm__title">
                  姓名
                </div>
                <input type="text" placeholder="姓名"
                v-model="newMail.name" required>
              </label>
            </div>
            <div class="myForm">
              <label for="tel">
                <div class="myForm__title">
                  聯絡電話
                </div>
                <input type="tel" placeholder="聯絡電話"
                v-model="newMail.tel" required>
              </label>
            </div>
            <div class="myForm">
              <label for="email">
                <div class="myForm__title">
                  E-mail
                </div>
                <input type="email" placeholder="E-mail"
                v-model="newMail.mail" required>
              </label>
            </div>
            <div class="myForm">
              <label for="content">
                <div class="myForm__title">
                  留言給我們
                </div>
                <textarea placeholder="留言給我們"
                v-model="newMail.content" required
                ></textarea>
              </label>
            </div>
            <div class="myForm myForm--df">
              <captcha :id='"contact__sendMessage"'
              @callVerification='verify'></captcha>
            </div>
            <div class="myForm">
              <button type="submit">送出</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import login from '@/components/Login.vue';
import captcha from '@/components/Captcha.vue';

export default {
  components: {
    login,
    captcha,
  },
  data() {
    return {
      newMail: null,
      captchaPass: false,
    };
  },
  computed: {
    user() {
      let user = {};
      this.$bus.$on('son_get_data', (data) => {
        user = data.member;
      });
      this.$bus.$emit('son_call_data');
      return user;
    },
  },
  methods: {
    createMail() {
      this.newMail = {
        id: this.user.id,
        time: Date.parse(new Date()),
        name: this.user.name,
        tel: this.user.tel,
        mail: this.user.email,
        content: '',
        read: false,
        reply: {
          id: '',
          time: '',
          name: '',
          content: '',
        },
      };
    },
    sendMail() {
      const timeNow = (new Date()).getTime();
      const mailTime = JSON.stringify(timeNow);
      if (this.captchaPass) {
        if (this.newMail.id === '') {
          const mailId = `mail${mailTime}`;
          this.newMail.id = mailId;
        } else {
          const mailId = `${this.user.id}${mailTime}`;
          this.newMail.id = mailId;
        }
        this.$bus.$emit('add_new_mail', this.newMail);
        alert('留言送出成功!');
        this.createMail();
      } else {
        alert('驗證碼錯誤');
      }
      this.$bus.$emit('create_new_code');
    },
    verify(result) {
      this.captchaPass = result;
    },
  },
  created() {
    this.$bus.$on('createMail', () => {
      this.createMail();
    });
    this.createMail();
  },
};
</script>
