<template>
  <div class="login">
    <div class="login__block">
      <div class="login__header">
        會員登入
      </div>
      <div class="login__body">
        <form @submit.prevent="loginVerify">
          <div class="myForm">
            <div class="myForm__title">
              Email
            </div>
            <label for="account">
              <input type="email" v-model="loginForm.email">
            </label>
          </div>
          <div class="myForm">
            <div class="myForm__title">
              密碼
            </div>
            <label for="password">
              <input type="password" v-model="loginForm.password">
            </label>
          </div>
          <div class="myForm__remark">
            <a href="#">忘記密碼?</a>
          </div>
          <div class="myForm">
            <button type="submit">登入</button>
          </div>
        </form>
      </div>
      <div class="login__header">
        FB登入
      </div>
      <div class="login__body">
        <a href="#" class="myBtn myBtn--primary" @click="fb_login(true)">
          FB登入
        </a>
      </div>
    </div>
    <div class="login__block">
      <div class="login__header">
        快速註冊會員
      </div>
      <div class="login__body">
        <form @submit.prevent="register">
          <div class="myForm">
            <div class="myForm__title">
              姓名
            </div>
            <label for="name">
              <input type="text" v-model="newMember.name">
            </label>
          </div>
          <div class="myForm">
            <div class="myForm__title">
              性別
            </div>
            <label for="gender">
              <input type="radio" name="gender" value="male" v-model="newMember.gender">
              <span>男生</span>
            </label>
            <label for="gender">
              <input type="radio" name="gender" value="female" v-model="newMember.gender">
              <span>女生</span>
            </label>
          </div>
          <div class="myForm">
            <div class="myForm__title">
              生日
            </div>
            <label for="birthday">
              <input type="date" v-model="newMember.birthday">
            </label>
          </div>
          <div class="myForm">
            <div class="myForm__title">
              電話
            </div>
            <label for="birthday">
              <input type="tel" v-model="newMember.tel">
            </label>
          </div>
          <div class="myForm">
            <div class="myForm__title">
              E-mail
            </div>
            <label for="birthday">
              <input type="email" v-model="newMember.email">
            </label>
          </div>
          <div class="myForm">
            <div class="myForm__title">
              密碼
            </div>
            <label for="birthday">
              <input type="password" v-model="newMember.password">
            </label>
          </div>
          <div class="myForm">
            <div class="myForm__title">
              確認密碼
            </div>
            <label for="birthday">
              <input type="password" v-model="confirmPassword">
            </label>
          </div>
          <div class="myForm">
            <captcha :id='"login__register"'
            @callVerification='verify'></captcha>
          </div>
          <div class="myForm">
            <button type="submit">註冊</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import captcha from '@/components/Captcha.vue';

export default {
  components: {
    captcha,
  },
  data() {
    return {
      loginForm: {
        email: '',
        password: '',
      },
      newMember: {
        id: '',
        name: '',
        gender: '',
        birthday: '',
        tel: '',
        email: '',
        password: '',
        favorites: [],
      },
      confirmPassword: '',
      captchaPass: false,
    };
  },
  computed: {
    members() {
      let members = [];
      this.$bus.$on('son_get_data', (data) => {
        members = data.members;
      });
      this.$bus.$emit('son_call_data');
      return members;
    },
  },
  methods: {
    createNewMember() {
      this.newMember = {
        id: '',
        name: '',
        gender: '',
        birthday: '',
        tel: '',
        email: '',
        password: '',
        favorites: [],
      };
    },
    register() {
      const repeatEmail = this.members
        .map((item) => item.email).includes(this.newMember.email);
      if (repeatEmail) {
        this.$bus.$emit('show_toast', 'warning', '此電子信箱已被註冊過，請登入會員或選擇其他電子信箱');
      } else if (!this.captchaPass) {
        this.$bus.$emit('show_toast', 'warning', '驗證碼錯誤');
      } else {
        this.newMember.id = `m_${(new Date()).yyyymmdd()}`;
        this.$bus.$emit('add_new_member', this.newMember);
        this.register_captcha_ok = 0;
        this.createNewMember();
      }
      this.$bus.$emit('create_new_code');
    },
    verify(result) {
      this.captchaPass = result;
    },
    loginVerify() {
      // this.loginForm
      const theMember = this.members
        .find((item) => item.email === this.loginForm.email);
      if (theMember === undefined) {
        this.$bus.$emit('show_toast', 'warning', '無此用戶');
      } else if (theMember.password !== this.loginForm.password) {
        this.$bus.$emit('show_toast', 'warning', '密碼錯誤');
        this.loginForm.password = '';
      } else {
        this.$bus.$emit('log_in', theMember);
        this.$bus.$emit('createMail');
      }
    },
    fb_login(boolean) {
      this.$bus.$emit('fb_login', boolean);
    },
  },
  created() {
  },
};
</script>
